"use client"

import { useState } from "react"
import { createBrowserClient } from "@supabase/ssr"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { SiteHeader } from "@/components/site-header"
import { SiteFooter } from "@/components/site-footer"

export default function BookingPage() {
  const [loading, setLoading] = useState(false)
  const [success, setSuccess] = useState(false)
  const [formData, setFormData] = useState({
    client_name: "",
    client_email: "",
    client_phone: "",
    apartment_type: "",
    move_in_date: "",
    move_out_date: "",
  })

  const supabase = createBrowserClient(
    process.env.NEXT_PUBLIC_SUPABASE_URL!,
    process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!,
  )

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)

    try {
      const bookingData = {
        client_name: formData.client_name,
        email: formData.client_email,
        phone_number: formData.client_phone,
        apartment_type: formData.apartment_type,
        start_date: formData.move_in_date,
        end_date: formData.move_out_date,
        status: "pending",
      }

      console.log("Submitting booking:", bookingData)

      const { error } = await supabase.from("bookings").insert([bookingData])

      if (error) {
        console.error("Booking error:", error)
        alert("Error submitting booking: " + error.message)
        setLoading(false)
        return
      }

      alert("Booking submitted successfully!")
      setSuccess(true)
      setFormData({
        client_name: "",
        client_email: "",
        client_phone: "",
        apartment_type: "",
        move_in_date: "",
        move_out_date: "",
      })

      setTimeout(() => setSuccess(false), 3000)
    } catch (err) {
      console.error("Error:", err)
      alert("An error occurred")
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-background">
      <SiteHeader />

      <main className="container mx-auto px-4 py-12">
        <div className="max-w-2xl mx-auto">
          <Card>
            <CardHeader>
              <CardTitle>Book an Apartment</CardTitle>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit} className="space-y-4">
                <div>
                  <label className="block text-sm font-medium mb-2">Full Name</label>
                  <Input
                    type="text"
                    placeholder="Your name"
                    value={formData.client_name}
                    onChange={(e) => setFormData({ ...formData, client_name: e.target.value })}
                    required
                  />
                </div> 

                <div>
                  <label className="block text-sm font-medium mb-2">Email</label>
                  <Input
                    type="email"
                    placeholder="Your email"
                    value={formData.client_email}
                    onChange={(e) => setFormData({ ...formData, client_email: e.target.value })}
                    required
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium mb-2">Phone Number</label>
                  <Input
                    type="tel"
                    placeholder="Your phone"
                    value={formData.client_phone}
                    onChange={(e) => setFormData({ ...formData, client_phone: e.target.value })}
                    required
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium mb-2">Apartment Type</label>
                  <select
                    className="w-full p-2 border rounded"
                    value={formData.apartment_type}
                    onChange={(e) => setFormData({ ...formData, apartment_type: e.target.value })}
                    required
                  >
                    <option value="">Select apartment type</option>
                    <option value="Studio">Studio</option>
                    <option value="1 Bedroom">1 Bedroom</option>
                    <option value="2 Bedroom">2 Bedroom</option>
                    <option value="3 Bedroom">3 Bedroom</option>
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium mb-2">Move-in Date</label>
                  <Input
                    type="date"
                    value={formData.move_in_date}
                    onChange={(e) => setFormData({ ...formData, move_in_date: e.target.value })}
                    required
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium mb-2">Move-out Date</label>
                  <Input
                    type="date"
                    value={formData.move_out_date}
                    onChange={(e) => setFormData({ ...formData, move_out_date: e.target.value })}
                    required
                  />
                </div>

                {success && <p className="text-green-600">Booking submitted successfully!</p>}

                <Button type="submit" disabled={loading} className="w-full">
                  {loading ? "Submitting..." : "Submit Booking"}
                </Button>
              </form>
            </CardContent>
          </Card>
        </div>
      </main>

      <SiteFooter />
    </div>
  )
}
